.header {
    display: flex;
    row-gap: 0.5rem;
    align-items: center;
    padding: 0.5rem;
    margin: 0 10rem;
    padding-top: 4rem;
    flex-wrap: wrap;
    .navcontainer {
        position: relative;
        display: flex;
        justify-content: flex-end;
        .burgerMenu {
            display: none;
            flex-direction: column;
            justify-content: space-between;
            height: 24px;
            width: 30px;
            cursor: pointer;
            span {
                width: 100%;
                height: 2px;
                background-color: black;
                transition: all 0.3s ease-in-out;
            }
            &.open {
                span:first-child {
                    transform: rotate(45deg) translate(10px, 5px);
                }
                span:last-child {
                    transform: rotate(-45deg) translate(10px, -5px);
                }
                span:nth-child(2) {
                    opacity: 0;
                }
            }
            &.close {
                span:first-child,
                span:last-child {
                    transform: none;
                }
                span:nth-child(2) {
                    opacity: 1;
                }
            }
        }
        .nabar {
            flex-direction: row;
            justify-content: end;
            @apply text-2xl gap-x-8;
            a:hover {
                color: grey;
                text-decoration: underline;
                text-underline-offset: 1rem;
                text-decoration-color: rgb(181, 181, 181);
            }
        }
        .showNavBar {
            display: flex !important;
            top: 2rem;
            right: 0;
            border: 1px solid rgb(225, 223, 221);
            padding: 0.5rem;
            border-radius: 0.5rem;
            box-shadow: 1px 2px #00000017;
        }
    }
}

@media screen and (max-width: 600px) {
    .header {
        margin: 0 0.5rem;
        .logo {
            width: fit-content;
        }
        .navcontainer {
            .burgerMenu {
                display: flex;
            }
            .nabar {
                position: absolute;
                flex-direction: column;
                display: none;
                background: white;
            }
        }
    }
}
